import React, {useEffect, useState} from 'react';
import { Container, Col, Row, Accordion, Button, Table, Image } from 'react-bootstrap';
import app from '../../config/base';
import {Link} from 'react-router-dom';
import firebase from 'firebase'
import CartItem from '../../components/cart/cart-item';


export default function OrderHistory({order, index}) {

    const db = firebase.firestore();

    const [showOrder, setShowOrder] = useState(true);


    let renderOrderTable = null
    if (showOrder){
        renderOrderTable = (
            order.map((item, key) => {
                return <tr key={key} >
                    <td>
                        <Image src={item.img} width='100px' />
                    </td>
                    <td>{item.title} {item.subtitle}</td>
                    <td>{item.qty}</td>
                    <td>{item.price}</td>
                </tr>
            })
        )
    } else {
        renderOrderTable = null
    }

    const showOrderHandler = () => {
        
    }
    


  return (
    <div>
        <h4 onClick={() => setShowOrder(!showOrder)} >Order {index + 1}</h4>
        <Table striped bordered hover>
                <thead>
                    <tr>
                    <th></th>
                    <th>Item(s)</th>
                    <th>Qty</th>
                    <th>Price</th>
                    </tr>
                </thead>
                <tbody>
                    {renderOrderTable}
                    <tr>
                        <td></td>
                        <td></td>
                        <td>Total</td>
                        <td>$.00</td>
                    </tr>
                </tbody>
        </Table>
        <Button>Buy Again</Button>
    </div>
  );
}

