import React, {useEffect, useState} from 'react';
import { Container, Col, Row, Accordion, Card, Table, Button } from 'react-bootstrap';
import app from '../../config/base';
import {Link} from 'react-router-dom';
import firebase from 'firebase'
import CartItem from '../../components/cart/cart-item';
import OrderHistory from './orderHistoryComponet';


export default function AccountInfo({cart, clearCart, setCart, removeItemHandler, priceTotal}) {

    const [userid, setUserid] = useState('');
    const [guide, setGuide] = useState([]);
    const [ordersArray, setOrdersArray] = useState();

    const db = firebase.firestore();



    useEffect(() => {
        // Update the document title using the browser API

        app.auth().onAuthStateChanged(function(user) {
            if (user) {
              // User is signed in.
                setUserid(user.email);
                getTestHandler();

                // db.collection('users').doc(user.uid).collection('orders').get().then( doc => {
                //     console.log(doc)
                //     // setUserid(doc.data().name)
                // })



                db.collection('users').doc(user.uid).collection('orders').get().then( snapshot => {


                    
                    let b = []

                    let a = snapshot.docs
                    a.forEach(doc => {
                        let NewOrder = doc.data().order

                        b.push(NewOrder)
                    });

                    setOrdersArray(b)
                    
                })

                
            } else {
              // No user is signed in.
                setUserid('Log In to see details')
                setGuide([])
            }
        });






    },[]);

    const getTestHandler = () => {
        db.collection('guides').get().then(snapshot => {
            let array = snapshot.docs
    
            array.forEach(doc => {
    
                setGuide(guide => [...guide, doc.data()])
    
            });
        })
    }

    let renderOrders = null


    if (ordersArray === undefined){
        
    } else if (ordersArray.length > 0) {
        renderOrders = (
            ordersArray.map((order, index) => {
                return <OrderHistory key={index} index={index} order={order} />
            })
        );
    }



  return (
    <div className='store' >
        <Container  >
            <Row>
                <Col>

                  <h5>Your Account Info</h5>
                  <h6>Name: {userid}</h6>
                  <h6>Shipping To: 1207 s Busey Ave, Urbana IL, 61801</h6>
                  <h6>Your Orders:</h6>
                  {renderOrders}

                </Col>
            </Row>
        </Container>
    </div>
  );
}

